
.rr-resizer {
  position: absolute;
  box-sizing: border-box;
  left: 0;
  top: 0;
  border: 1px solid #bbb;
}
.rr-resizer.rr-resizer--active {
  border: 1px solid #08a1ef;
}
.rr-resizer--active .rr-rotatable-handle {
  display: block;
}
.rr-resizer--active .rr-resizable-handle {
  display: block;
}
.rr-resizer-content {
  width: 100%;
  height: 100%;
}
.rr-rotatable-handle {
  display: none;
  position: absolute;
  text-align: center;
  left: 50%;
  top: -30px;
  border: 1px solid #08a1ef;
  box-sizing: border-box;
  width: 10px;
  height: 10px;
  background: #fff;
  border-radius: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  cursor: crosshair;
}
.rr-rotatable-handle::after {
  content: "";
  position: absolute;
  border-left: 1px solid #08a1ef;
  width: 0;
  top: 8px;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  height: 25px;
}
.rr-resizable-handle {
  display: none;
  box-sizing: border-box;
  position: absolute;
  border: 1px solid #08a1ef;
  width: 10px;
  height: 10px;
  background: #fff;
  border-radius: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.rr-resizable-n {
  cursor: n-resize;
  left: 50%;
  top: 0;
}
.rr-resizable-s {
  cursor: s-resize;
  bottom: 0;
  left: 50%;
  -webkit-transform: translate(-50%, 50%);
          transform: translate(-50%, 50%);
}
.rr-resizable-e {
  cursor: e-resize;
  right: 0;
  top: 50%;
  -webkit-transform: translate(50%, -50%);
          transform: translate(50%, -50%);
}
.rr-resizable-w {
  cursor: w-resize;
  left: 0;
  top: 50%;
}
.rr-resizable-nw {
  cursor: nw-resize;
  left: 0;
  top: 0;
}
.rr-resizable-ne {
  cursor: ne-resize;
  right: 0;
  top: 0;
  -webkit-transform: translate(50%, -50%);
          transform: translate(50%, -50%);
}
.rr-resizable-se {
  cursor: se-resize;
  bottom: 0;
  right: 0;
  -webkit-transform: translate(50%, 50%);
          transform: translate(50%, 50%);
}
.rr-resizable-sw {
  cursor: sw-resize;
  bottom: 0;
  left: 0;
  -webkit-transform: translate(-50%, 50%);
          transform: translate(-50%, 50%);
}



